			}
		}
	},
	data() {
		return {
		};
	},
	onLoad() {
	},
	methods: {
		//第三方登录
		toOtherLogin(item) {
			console.log('other-login',JSON.stringify(item))
			uni.login({
				provider: item.id,



				success: res => {
					console.log('login success:', JSON.stringify(res));
					// 更新保存在 store 中的登录状态

					// this.login(provider.id
					console.log('登录成功');
